import React from 'react'
import zigzag1 from '../block/zigzag design 1.jpg';
import zigzag2 from '../block/zigzag design 2.jpg';
import zigzag3 from '../block/zigzag design 3.jpg';
import cosmic1 from '../block/cosmic-paver-basic-block-design.jpg'
import cosmic2 from '../block/cosmic-paver-basic-block-design2.jpeg'
import cosmic3 from '../block/cosmic-paver-basic-block-design3.png'
import rectangle1 from '../block/rectangle design 1.jpg'
import rectangle2 from '../block/rectangle design 2.jpg'
import rectangle3 from '../block/rectangle design 3.jpg'
import rectangle4 from '../block/rectangle design 4.jpg'

const Gallery = () => {
    return (
        <div className='bg-[#f0f0f0] w-full py-[50px] mt-10 rounded-[100px] font-[cursive] sm:bg-[#d3e9e9a1]' id='galary'>
            {/* name */}
            <p className='font-bold text-[55px] text-center sm:text-[40px]'>
                <span className='border-y-[10px] border-[grey] rounded-[30px] sm:border-[blue]'>
                    Galary
                </span>
            </p>

            <div className="py-[40px] mt-[40px] px-4">
                <div className="max-w-[1240px] mx-auto grid grid-cols-3 gap-10 sm:flex sm:flex-col sm:gap-[4px]">
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={zigzag1} alt="ZigZag Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={zigzag2} alt="ZigZag Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={zigzag3} alt="ZigZag Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    {/* <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={cosmic1} alt="Cosmic Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' />
                    </div> */}
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={cosmic2} alt="Cosmic Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={cosmic3} alt="Cosmic Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={rectangle1} alt="Rectangle Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={rectangle2} alt="Rectangle Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={rectangle3} alt="Rectangle Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                    <div className="border border-[black] border-[3px] shadow-xl hover:scale-90 duration-500 my-5 text-center bg-[grey] rounded-[55px]" >
                        <img src={rectangle4} alt="Rectangle Paver Block Design" className='bg-transparent h-[320px] w-full rounded-[55px]' loading='lazy' decoding='async'/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export  default Gallery;